.app-about-me{
  color: #64F4AB;
  padding: 0 10px ;
  background: rgba(0, 8, 2, 0.692);
  height: 100%;
  overflow: auto;
  &__body{
    display: flex;
  }
  &__column-pnoto{
    width: 35%;
  }
  &__column-text{
    width: 65%;
  }
}

.column-pnoto{
  &__photo{
    img {
      height: 100%;
      width: 100%;
      object-fit: cover;
    }
  }
}